export default function stationEditForm() {
	const config = window.appConfig || {};

  let formItems = {
    identity: {
      rules: [
        {
          required: true,
        },
      ],
    },
    label: {
      rules: [
        {
          required: true,
        },
      ],
    },
    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("typename"),
      component: (
        <PlainSelect labelKey="name" cache query="/charge_point/type" />
      ),
    },
    vendor: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("vendorname"),
      component: <PlainSelect enabledOnly cache query="/vendor/list" />,
    },
    model: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("modelname"),
      component: <PlainSelect enabledOnly cache query="/model/list" />,
    },
    network: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("networkname"),
      component: <PlainSelect enabledOnly cache query="/network/list" />,
    }
  };
 let newForm = config.confAdmin ? {...formItems, client: {
	label: this.m("client"),
	component: <PlainSelect labelKey="label" cache query="/client/list" />,
  }, serialnumber: {},
  boxserialnumber: {},
  iccid: {},
  imsi: {},
  description: {
	component: <TextArea />,
  },} : {...formItems, serialnumber: {},
  boxserialnumber: {},
  iccid: {},
  imsi: {},
  description: {
	component: <TextArea />,
  },};
  return (
    <>
      {Object.entries(newForm).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
