import {Card, Empty, Tag} from "antd";
import React from "react";
import { injectIntl } from "react-intl";
import { Row, Col } from "antd";
import Statelabel from "../../util/Statelabel";
import Flex from "../../util/Flex";
import StationIcon from "./StationIcon";
import { Link } from "react-router-dom";
class StationsTiles extends React.Component {
  m = (key, prefix = "stations.tiles.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  render() {
    const { stations } = this.props;

    if (!Array.isArray(stations)) return null;
    if (stations.length === 0) return <Empty />;
    return (
      <>
        <Row gutter={[16, 16]}>
          {stations.map((item) => {
            return (
              <Col key={item.id} xl={6} lg={8} sm={12} xs={24}>
                <Card className="gx-mb-0 gx-h100">
                  <Flex column between alignItems="start" className="gx-h100">
                    <div>
                      <Link to={`/crm/station/${item.id}`} className="gx-link">
                        <Flex
                          between
                          className="gx-w100 gx-pt-2 gx-mt-2"
                        >
                        <StationIcon {...item} />
                          <div className="gx-ml-2 gx-mr-4">
                            <div>{item.label}</div>
                            {item.description && (
                              <div className="gx-font-weight-light gx-small gx-text-muted ">
                                {item.description}
                              </div>
                            )}
                          </div>
                        </Flex>
                      </Link>
                    </div>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2 gx-mt-2"
                    >
                      {item.vendorname && <div className="gx-mr-4">{item.vendorname}</div>}
                      {item.serialnumber && <div>{item.serialnumber}</div>}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2"
                    >
                      {item.modelname && <div className="gx-mr-4">{item.modelname}</div>}
                      {item.version && <div>{item.version}</div>}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-pt-2 gx-mt-2"
                    >
                      <div className="gx-mr-4">{item?.metadata?.Address}</div>
                      {item?.metadata?.Connection?.IP && (
                        <div className="gx-text-muted gx-font-weight-right">
                          {item?.metadata?.Connection?.IP}:{item?.metadata?.Connection?.Port}/{item?.metadata?.Connection?.Socket}
                        </div>
                      )}
                    </Flex>

                    <Flex
                      between
                      className="gx-w100 gx-border-top gx-pt-3 gx-mt-2"
                    >
                      <div className="gx-mr-4">{item.typename}</div>
                      <Flex>
                        <Tag className="gx-m-1" color={item.connected ? "success" : "error"}>
                          {item.connected ? this.m("online") : this.m("offline")}
                        </Tag>

                        <Statelabel
                          {...item}
                          onFinish={this.props.onMethodFinish}
                        />
                      </Flex>
                    </Flex>
                  </Flex>
                </Card>
              </Col>
            );
          })}
        </Row>
      </>
    );
  }
}
export default injectIntl(StationsTiles);
