import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import { Link } from "react-router-dom";
import {Tag} from "antd";
class StationsTable extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }
  m = (key, prefix = "stations.table.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          title: this.m("identity"),
          dataIndex: "identity",
          text: true,
          sorter: true,
          width: "15%",
          defaultSortOrder: true,
          sortDirections: "ascend",

          render: (value, item) => (
            <Link to={`/crm/station/${item.id}`}>{value}</Link>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("label"),
          dataIndex: "label",
          text: true,
          sorter: true,
          width: "15%",

          render: (value, item) => (
            <>
              <p>{value}</p>
              {item.description && (
                <div className="gx-text-muted gx-font-weight-light">
                  {item.description}
                </div>
              )}
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("connection"),
          key: "metadata",
          dataIndex: "metadata",
          width: "10%",
          render: (value, item) => (
            <>
              {value?.Address}
              {value?.Connection?.IP && (
                <div className="gx-text-muted gx-font-weight-light">
                  {value?.Connection?.IP}:{value?.Connection?.Port}/{value?.Connection?.Socket}
                </div>
              )}
            </>
          ),
        },
        {
          title: this.m("clientname"),
          dataIndex: "clientname",
          text: true,
          sorter: true,
          width: "15%",
          render: (value, item) => <>
            <Link to={`/crm/clients/${item.client}`}>{value}</Link>

            {item.clientcode && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.clientcode}
              </div>
            )} </>,

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("typename"),
          dataIndex: "typecode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>{item.typename}</>,
          filters: [
            {
              text: this.m("type.public"),
              value: "public.charge_point",
            },
            {
              text: this.m("type.private"),
              value: "private.charge_point",
            },
          ],
        },
        {
          title: this.m("network"),
          dataIndex: "networkname",
          text: true,
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            {value}
            {item.networkdescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.networkdescription}
              </div>
            )} </>,

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("vendor"),
          dataIndex: "vendorname",
          sorter: true,
          text: true,
          width: "10%",
          render: (value, item) => (
            <>
              {value}
              {item.vendordescription && (
                <div className="gx-text-muted gx-font-weight-light">
                  {item.vendordescription}
                </div>
              )}
            </>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("model"),
          dataIndex: "modelname",
          sorter: true,
          text: true,
          width: "10%",
          render: (value, item) => (
            <>
              {value}
              {item.modeldescription && (
                <div className="gx-text-muted gx-font-weight-light">
                  {item.modeldescription}
                </div>
              )}
            </>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("serialnumber"),
          dataIndex: "serialnumber",
          sorter: true,
          text: true,
          width: "15%",
          render: (value, item) => (
            <>
              {value}
              {item.boxserialnumber && item.boxserialnumber !== value && (
                <div className="gx-text-small gx-text-muted">
                  {item.boxserialnumber}
                </div>
              )}
            </>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          title: this.m("connectionupdate"),
          dataIndex: "connectionupdate",
          sorter: true,
          width: "10%",
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <div className="gx-text-center">
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </div>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "5%",
          title: this.m("connected"),
          dataIndex: "connected",
          defaultSortOrder: true,
          sortDirections: "descend",
          sorter: true,
          render: (value, item) =>
            <div className="gx-text-center">{
              <Tag className="gx-m-1" color={item.connected ? "success" : "error"}>
                {item.connected ? this.m("online") : this.m("offline")}
              </Tag>
            }
            </div>,
          filters: [
            {
              text: 'Yes',
              value: true,
            },
            {
              text: 'No',
              value: false,
            },
          ],
        },
        {
          title: this.m("statename"),
          dataIndex: "statecode",
          equal: true,
          sorter: true,
          width: "5%",
          render: (value, item) => (
            <>
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </>
          ),
          filters: [
            {
              text: this.m("created"),
              value: 'created',
            },
            {
              text: this.m("available"),
              value: 'available',
            },
            {
              text: this.m("unavailable"),
              value: 'unavailable',
            },
            {
              text: this.m("faulted"),
              value: 'faulted',
            },
            {
              text: this.m("disabled"),
              value: 'disabled',
            },
            {
              text: this.m("deleted"),
              value: 'deleted',
            },
          ],
        },
      ],
    });
  };

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  render() {
    return (
      <>
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/charge_point/list"}
          forceOrder={["connected DESC", "identity"]}
        />
      </>
    );
  }
}

export default injectIntl(StationsTable);
